.sortable-list{width:500px;max-width:100%;border:solid 1px #ccc;min-height:60px;display:block;background:#fff;border-radius:4px}.sortable-item{padding:20px 10px;border-bottom:solid 1px #ccc;color:rgba(0,0,0,0.87);background:#fff;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.sortable-slide-animation{-webkit-transition:-webkit-transform 0.2s;transition:-webkit-transform 0.2s;transition:transform 0.2s;transition:transform 0.2s, -webkit-transform 0.2s}.sortable-item-dragging{pointer-events:none}.draggable-element{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;-webkit-transition:-webkit-box-shadow 0.5s;transition:-webkit-box-shadow 0.5s;transition:box-shadow 0.5s;transition:box-shadow 0.5s, -webkit-box-shadow 0.5s;position:relative}.draggable-cursor-grab{cursor:-webkit-grab;cursor:grab}.dragging{cursor:move;position:relative;z-index:9000 !important}.draggable-disabled{cursor:default}.draggable-return-animate{-webkit-transition:-webkit-transform 0.35s;transition:-webkit-transform 0.35s;transition:transform 0.35s;transition:transform 0.35s, -webkit-transform 0.35s;position:relative;z-index:9000 !important}
